@import url(https://fonts.googleapis.com/css2?family=Cairo&display=swap:400,700,600);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:#ffd0b0;font-family:"Cairo",sans-serif;font-weight:400}*{margin:0;padding:0;box-sizing:border-box}*:active,*:focus{border:0;outline:0}a{text-decoration:none;display:inline-block}#card{width:25rem;margin:auto;overflow:hidden;position:absolute;top:50%;left:50%;transform:translateX(-50%) translateY(-50%)}.search--box label{position:relative}.search--box label:after{position:absolute;content:"";border-right:.625rem solid transparent;border-left:.625rem solid transparent;border-bottom:.625rem solid #fff;bottom:-1rem;right:50%;margin-right:-0.3125rem}.search--box input[type='search']{width:100%;font:1rem "Cairo",sans-serif;color:#c96f53;border:0;padding:.75rem .9375rem .9375rem;background:#ffcaa6;border-radius:.3125rem .3125rem 0 0;text-align:center}.notfound{background:#fff;padding:1.875rem;text-align:center}.notfound h2{font-size:2rem;color:#d6937e;margin-bottom:.625rem}.notfound p{font-size:1rem;color:#ff8431}.github--profile__info{background:#fff;text-align:center;padding:1.875rem .9375rem}.github--profile__info img{width:6.25rem;height:6.25rem;border-radius:50%;display:block;box-shadow:0 0 0.0625rem rgba(0,0,0,0.5)}.github--profile__info h2 a{font-size:1.65rem;color:#d6937e}.github--profile__info h3{font-size:1.1885rem;color:#ff9f5e}.github--profile__state{background:#c96f53;text-align:center;padding:1.875rem .9375rem;border-radius:0 0 .3125rem .3125rem}.github--profile__state ul{direction:ltr}.github--profile__state li{list-style:none;display:inline-block;margin-right:1rem}.github--profile__state li:last-child{margin-right:0}.github--profile__state a{color:#fff}.github--profile__state i{font-size:1.5rem;font-weight:700;font-style:normal;display:block}.github--profile__state span{font-size:.844rem;letter-spacing:.0625rem;color:#dea695}.taraFooter{font-size:.9rem;font-weight:400;color:#ff964f;position:absolute;bottom:3%;left:50%;transform:translateX(-50%)}.taraFooter a{color:#d6937e}

